<template>
  <div>
    <loading :active.sync="isLoading">
      <Circle4/>
    </loading>
    <div class="contact-banner bg-setting"></div>
    <section class="section">
      <div class="container">
        <div class="row">
          <h3 class="col-md-8 offset-md-2 mb-3">聯絡我們</h3>
        </div>
        <div class="row">
          <ValidationObserver ref="form" class="contact-content col-md-8 offset-md-2 mb-3">
            <div class="form-row">
              <ValidationProvider
                v-slot="{ failed, errors }"
                name="name"
                rules="required"
                class="col-md-6 mb-3">
                <label for="userName">姓名</label>
                <input
                  type="text"
                  class="form-control"
                  :class="{ 'is-invalid': failed }"
                  id="userName"
                  placeholder="姓名"
                  v-model="name">
                <span v-if="failed" class="text-danger">{{ errors[0] }}</span>
              </ValidationProvider>
              <div class="col-md-6 mb-3">
                <label for="userPhone">電話</label>
                <input type="text" class="form-control" id="userPhone" placeholder="電話" v-model="phone">
              </div>
            </div>
            <div class="form-group">
              <ValidationProvider
                v-slot="{ failed, errors }"
                name="email"
                rules="required"
                class="col-md-6 mb-3">
                <label for="userEmail">Email</label>
                <input
                  type="text"
                  class="form-control"
                  :class="{ 'is-invalid': failed }"
                  id="userEmail"
                  placeholder="電子郵件"
                  v-model="email">
                <span v-if="failed" class="text-danger">{{ errors[0] }}</span>
              </ValidationProvider>
            </div>
            <div class="form-group">
              <label for="userInfo">留言</label>
              <textarea class="form-control contact-text-area" id="userInfo" v-model="contact"></textarea>
            </div>
            
          </ValidationObserver>
        </div>
        <div class="row">
          <div class="col-md-4 offset-md-4">
            <button class="btn btn-primary btn-block" @click="onSubmit">送出</button>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
import { Circle4 } from 'vue-loading-spinner'

export default {
  name: 'Contact',
  components:{
    Circle4
  },
  data(){
    return{
      name: '',
      phone: '',
      email: '',
      contact: '',
      isLoading: false
    }
  },
  methods:{
    onSubmit () {
      this.$refs.form.validate().then((success) => {
        if (success) {
          console.log('Success')
        } else {
          console.log('Falut')
        }
      });
    },
  },
  created(){
    this.isLoading = true
  },
  mounted(){
    this.isLoading = false
  }
}
</script>
<style lang="scss">
@import '../assets/scss/all';

.contact-banner {
  width: 100%;
  height: 400px;
  background-image: url('../assets/images/contactBanner.jpg');
}

.form-control {
  padding: 10px;
}

textarea.contact-text-area{
  height: 250px;
  resize: none;
}
</style>